import React, { FC } from "react";
import { getErrorMessage } from "../../utils/helpers";
import errorStyles from '../../pages/main-styles.module.css'

type TErrorDetails = {
  message: string
}

const ErrorDetails: FC<TErrorDetails> = ({ message }) => {
  const errorMessage = getErrorMessage(message)
  console.log(errorMessage)
  return (
    <p className={`${errorStyles.error} text text_type_main-default`}>{errorMessage}</p>
  )
}

export default ErrorDetails