import React, { FC } from "react";
import { motion } from "framer-motion";
import { useState, useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../utils/hooks/app-hooks";
import { useNavigate, useLocation } from "react-router-dom";
import { Input, Button, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components'
import pageStyles from '../main-styles.module.css'
import LinkWrapper from "../../components/link-wrapper/link-wrapper";
import { emailValidator, passwordValidator } from "../../utils/validation";
import { login } from "../../services/thunks/userThunks";
import { useForm } from "../../utils/hooks/use-form-hook";
import { getCookie } from "../../utils/cookie";
import { TUserData } from "../../utils/types/types";
import ErrorDetails from "../../components/error-details/error-details";
import { resetError } from "../../services/slices/user-slice";
import { useRef } from "react";

const LogInPage: FC = () => {
  const isLoggedIn = useAppSelector(store => store.user.isLoggedIn)
  const errorMessage = useAppSelector(store => store.user.errorMessage);

  const formState = {
    email: '',
    password: ''
  }
  const { values, handleChange } = useForm<TUserData>(formState)

  const [isEmailValid, setEmailValidity] = useState(true)
  const [isPasswordValid, setPasswordValidity] = useState(true)
  const [isPasswordShowed, setPasswordShow] = useState(false)

  const dispatch = useAppDispatch() 
  const navigate = useNavigate()
  const location = useLocation()

  const showPassword = () => {
    isPasswordShowed ? setPasswordShow(false) : setPasswordShow(true)
  }

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value, validity: { valid } } = e.target
    handleChange(e)
    setEmailValidity(!!valid && emailValidator(value))
    clearErrorMessage()
  }
  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value, validity: { valid } } = e.target
    handleChange(e)
    setPasswordValidity(!!valid && passwordValidator(value))
    clearErrorMessage()
  }

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    dispatch(login(values))
  }
  useEffect(() => {
    if(isLoggedIn || getCookie("access")) {
      navigate(location.state?.from ? location.state?.from : '/', {replace: true})
    }
  }, [isLoggedIn, navigate, getCookie])

  const clearErrorMessage = () => {
    if(!!errorMessage) {
      dispatch(resetError())
    }
  }

  useEffect(() => {
    return () => {
      dispatch(resetError())
    }
  }, [dispatch])

  const isFormEmpty = values.email === '' || values.password === '' ? true : null
  return (
    <motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
    <main className={pageStyles.wrapper_lg}>
     {!!errorMessage && <ErrorDetails message={errorMessage}/>}
      <form className={`${pageStyles.form_wrapper} mb-15`} onSubmit={handleSubmit}>
        <h1 className="text text_type_main-medium"></h1>
        <Input
          type={"email"}
          name={"email"}
          placeholder={"E-mail"}
          value={values.email}
          error={!isEmailValid || !!errorMessage}
          errorText={!!errorMessage? '' : " "}
          onChange={handleEmailChange} />
        <Input
          type={isPasswordShowed ? "text" : "password"}
          name={"password"}
          icon={isPasswordShowed? "HideIcon" : "ShowIcon"}
          placeholder={""}
          error={!isPasswordValid || !!errorMessage}
          value={values.password}
          errorText={!!errorMessage? '' : "    5 "}
          onChange={handlePasswordChange} 
          onIconClick={showPassword}/>
        <Button htmlType="submit" type="primary" size="medium" disabled={isFormEmpty || !isEmailValid || !isPasswordValid || !!errorMessage}>
          
        </Button>
      </form>
      <LinkWrapper link_to={'/register'} link_text={''} caption={'  ?'} />
      <LinkWrapper link_to={'/forgot-password'} link_text={' '} caption={' '} />
    </main>
    </motion.div>
  )
}

export default LogInPage