import React, { FC } from "react";
import { useEffect, useState } from "react";
import figuresStyles from './board-figures.module.css'

interface IBoardFigures {
  title: string
  figure: number | string
}
const BoardFigures: FC<IBoardFigures> = ({title, figure}) => {
  const [windowHeight, setWindowHeignt] = useState<number>(0)
  useEffect(() => {
    setWindowHeignt(window.innerHeight)
  }, [window.innerHeight])

  return (
    <div className={figuresStyles.wrapper}>
      <h2 className="text text_type_main-medium">{title}:</h2>
      <p className={`text ${windowHeight < 800 ?" text_type_digits-default ": 'text_type_digits-large' } text-shadow`}>{figure}</p>
    </div>
  )
}

export default BoardFigures